@import url("https://fonts.googleapis.com/css2?family=Oswald:wght@400;500&display=swap");
@font-face {
  font-family: "lato";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/lato/Lato-Regular.woff2"), url("../../fonts/lato/Lato-Regular.woff");
}
@font-face {
  font-family: "oswaldmedium";
  font-display: swap;
  font-weight: 500;
  font-style: normal;
  src: url("../../fonts/oswald/oswaldmedium.woff2"), url("../../fonts/oswald/oswaldmedium.woff");
}
@font-face {
  font-family: "oswald";
  font-display: swap;
  font-weight: 400;
  font-style: normal;
  src: url("../../fonts/oswald/oswaldregular.woff2"), url("../../fonts/oswald/oswaldregular.woff");
}
*,
*::after,
*::before {
  box-sizing: border-box;
}

body {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.container {
  padding: 0 20px;
  width: 100%;
  max-width: 1260px;
}

@media (min-width: 768px) {
  .container {
    padding: 0 30px;
  }
}
.page {
  height: 100%;
}

.page__wrapper {
  min-height: 100%;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  align-content: start;
  margin: 0;
  padding: 0;
  font-family: "lato", Arial, sans-serif;
  font-size: 14px;
  line-height: 18px;
  color: #444444;
}

.header {
  background-color: #FFFFFF;
}

.header__logo {
  display: block;
  padding: 10px 0 17px 0;
}

.main__nav {
  background-color: #FFFFFF;
}

.main-nav__toggle {
  position: absolute;
  top: -46px;
  right: 0px;
  border: none;
  background-color: transparent;
  cursor: pointer;
  width: 24px;
  height: 24px;
}

.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0px;
  right: 0px;
  width: 24px;
  height: 1.5px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}

.main-nav--opened .main-nav__toggle::before, .main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 5px;
  right: 0px;
  width: 30px;
  height: 1.5px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
  box-shadow: none;
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}

.main-nav__list {
  list-style: none;
  margin: 0;
  padding: 0;
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  align-items: center;
  text-transform: uppercase;
}

.main-nav__item {
  width: 100%;
}

.main-nav__link {
  font-family: "oswald", Arial, sans-serif;
  font-size: 20px;
  line-height: 20px;
  color: #000000;
  font-weight: 500;
  text-decoration: none;
  display: block;
  width: 100%;
}

@media (max-width: 767px) {
  .main-nav {
    position: relative;
  }

  .main-nav__item {
    width: 100%;
    text-align: center;
    border-bottom: solid 2px #E6E6E6;
  }
  .main-nav__item:first-child {
    border-top: solid 2px #E6E6E6;
  }
  .main-nav__item:last-child {
    border-top: solid 1px #E6E6E6;
  }

  .main-nav--closed .main-nav__list {
    display: none;
  }

  .main-nav--nojs .main-nav__toggle {
    display: none;
  }

  .main-nav__link {
    padding: 22px 20px 24px 20px;
  }
  .main-nav__link:hover {
    opacity: 0.7;
  }
  .main-nav__link:focus {
    outline: none;
    color: #5EAA2F;
  }
  .main-nav__link:active {
    color: rgba(104, 183, 56, 0.3);
  }
}
.hero {
  margin: 0 0 20px 0;
  position: relative;
}

.hero__wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.hero__wrapper::before {
  position: absolute;
  content: "";
  left: 0;
  top: 0;
  width: 100%;
  min-height: 302px;
  background: linear-gradient(rgba(104, 183, 56, 0.85), rgba(104, 183, 56, 0.85)), url("../../img/mobile/hero-bg-cat.png") center/cover;
  z-index: -1;
}

/*.hero__wrap {
  padding: 27px 20px 29px 20px;
  color: $basic-white;
  font-family: "oswald", Arial, sans-serif;
  font-weight: 400;
}*/
.hero__heading {
  padding: 27px 5px 0 5px;
  margin: 0 0 25px 0;
  text-align: center;
  font-size: 36px;
  line-height: 36px;
  font-family: "oswald", Arial, sans-serif;
  font-weight: 400;
  color: #FFFFFF;
}

.hero__text {
  margin: 0 0 29px 0;
  padding: 5px;
  text-align: center;
  text-transform: uppercase;
  color: #FFFFFF;
  font-family: "oswald", Arial, sans-serif;
  font-weight: 400;
}

.hero__product-img {
  display: flex;
}

.hero__button {
  padding: 7px;
  width: 100%;
  display: flex;
  justify-content: center;
  font-size: 14px;
  line-height: 18px;
  text-decoration: none;
}

.button {
  color: #FFFFFF;
  font-family: "oswald", Arial, sans-serif;
  background-color: #68B738;
  text-transform: uppercase;
}

.button:hover {
  background-color: #5EAA2F;
}

.feature {
  background-color: #FFFFFF;
  margin: 0 0 20px 0;
}

.feature__block {
  display: flex;
  flex-direction: column;
  background-color: #F2F2F2;
  padding: 21px 20px 22px 18px;
  margin: 0 0 20px 0;
}

.feature__block-wrap {
  display: flex;
  align-items: center;
  margin: 0 0 22px 0;
}

.feature__block-icon {
  z-index: 5;
  position: absolute;
  top: -3px;
  right: 50%;
  transform: translateX(50%);
}

.feature__block-icon--jock {
  top: 10px;
}

.feature__wrap-svg {
  display: block;
  width: 50px;
  height: 50px;
  background-color: #68B738;
  z-index: 0;
  border-radius: 50%;
  position: relative;
  margin: 0 19px 0 0;
}

.feature__block-heading {
  font-family: "oswald", Arial, sans-serif;
  font-size: 24px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 37px;
  margin: 0;
  color: #000000;
}

.feature__block-text {
  display: block;
  margin: 0;
  padding: 0 0 23px 0;
  border-bottom: solid 1px #D9D9D9;
}

.feature__block-link {
  display: block;
  text-decoration: none;
  font-family: "oswald", Arial, sans-serif;
  font-size: 16px;
  font-weight: 400;
  text-transform: uppercase;
  line-height: 16px;
  color: #444444;
  padding: 19px 0 0 0;
}

.feature__block-link-text {
  margin: 0 15px 0 0;
}

.about {
  margin: 0 0 49px 0;
}

.about__heading {
  margin: 0 0 40px 0;
  font-family: "oswald", Arial, sans-serif;
  font-weight: 400;
  font-size: 36px;
  line-height: 40px;
  color: #000000;
}

.about__list {
  list-style: none;
  padding: 0;
  margin: 0;
  display: flex;
  flex-direction: column;
}

.about__item {
  display: flex;
  margin: 0 0 20px 0;
  position: relative;
  align-items: center;
}

.about__item-icon-wrap {
  display: grid;
  grid-template-columns: 60px 1fr;
  justify-items: center;
  align-items: center;
  width: 60px;
  height: 60px;
  z-index: -1;
  background-color: #68B738;
}

.about__item-text {
  margin: 0 0 0 20px;
}

.truestory {
  background-color: #EAEAEA;
}

.truestory__heading {
  margin: 0 0 40px 0;
  padding: 25px 0 0 0;
  font-family: "oswald", Arial, sans-serif;
  font-weight: 400;
  font-size: 36px;
  color: #000000;
  line-height: 40px;
}

.truestory__text {
  margin: 0 0 20px 0;
}

.truestory__table-wrapper {
  margin: 0 0 34px 0;
}

.truestory__table {
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 32px;
  margin: 0;
}

.truestory__table-block {
  display: flex;
  flex-direction: column;
  border: solid 1px #CDCDCD;
  border-radius: 3px;
  padding: 14px 5px 0 5px;
  justify-content: center;
  align-items: center;
}

.truestory__table-result {
  font-family: "oswald", Arial, sans-serif;
  font-weight: 400;
  font-size: 24px;
  line-height: 24px;
  text-transform: uppercase;
  margin: 0 0 8px 0;
  color: #000000;
  text-align: center;
}

.truestory__table-text {
  font-size: 12px;
  line-height: 12px;
  margin: 0 0 -10px 0;
  background-color: #EAEAEA;
  max-width: 70px;
  text-align: center;
}

.truestory__cost {
  display: flex;
  font-family: "oswald", Arial, sans-serif;
  font-weight: 400;
  text-transform: uppercase;
  justify-content: center;
  margin: 0 0 20px 0;
}

.truestory__proof {
  padding: 0 0 40px 0;
}

.advert {
  padding: 26px 0 20px 0;
  display: grid;
  grid-template-columns: 1fr 1fr;
  gap: 5px;
  background-color: #FFFFFF;
}

.advert__heading {
  margin: 0;
  font-family: "oswald", Arial, sans-serif;
  font-size: 16px;
  line-height: 20px;
  font-weight: 400;
  text-transform: uppercase;
  color: #111111;
}

.advert__address {
  line-height: 20px;
  margin: 0;
}

.map__google {
  display: block;
}

.footer__wrap {
  background-color: #F2F2F2;
  padding: 40px 0;
}

.footer__bottom-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.footer__logo {
  display: block;
  margin: 0 0 20px 0;
}

.footer__social-list {
  list-style: none;
  width: 100%;
  margin: 0;
  padding: 23px 0 22px 0;
  display: flex;
  border-top: solid 1px #D9D9D9;
  border-bottom: solid 1px #D9D9D9;
  justify-content: center;
}

.footer__social-item {
  margin: 0 28px 0 0;
}
.footer__social-item:last-child {
  margin: 0;
}

.footer__authors {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 20px 0 0 0;
  width: 100%;
}